// MARGINS
.mx-auto {
	margin-left: auto;
	margin-right: auto;
}

@for $i from 0 through 4 {
	.m-#{$i} {
		margin:calc(#{$i} * var(--spacer));
	}

	.mb-#{$i} {
		margin-bottom:calc(#{$i} * var(--spacer));
	}

	.mt-#{$i} {
		margin-top: calc(#{$i} * var(--spacer));
	}

	.my-#{$i} {
		@extend .mt-#{$i};
		@extend .mb-#{$i};
	}

	// padding
	.p-#{$i} {
		padding:calc(#{$i} * var(--spacer));
	}

	.pb-#{$i} {
		padding-bottom:calc(#{$i} * var(--spacer));
	}

	.pt-#{$i} {
		padding-top: calc(#{$i} * var(--spacer));
	}

	.px-#{$i} {
		padding-left: calc(#{$i} * var(--spacer));
		padding-right: calc(#{$i} * var(--spacer));
	}

	.pl-#{$i} {
		padding-left: calc(#{$i} * var(--spacer));
	}

	.pr-#{$i} {
		padding-right: calc(#{$i} * var(--spacer));
	}

	.py-#{$i} {
		@extend .pt-#{$i};
		@extend .pb-#{$i};
	}
}


// PADDING
.p-0 {
	margin: 0 !important;
}

.p-1 {
	padding: var(--spacer);
}

.pb-1 {
	padding-bottom: var(--spacer);
}

.pt-1 {
	padding-top: var(--spacer);
}

.py-1 {
	padding-top: var(--spacer);
	padding-bottom: var(--spacer);
}

.px-1 {
	padding-left: var(--spacer);
	padding-right: var(--spacer);
}